import React from 'react'
import { Box, Button } from '@mui/material'
import useStyles from './PartnersForm.styles'
import { ControlledTextField } from '../../../Sub/FormElements'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setCurrentTab, setFormData } from '../../../../services/slices/partners-form-slice'

function TabPF4({ hookFormMethods }) {
  const dispatch = useAppDispatch()
  const { formData } = useAppSelector((state) => state.partnersForm)
  const classes = useStyles()
  const { control, handleSubmit, getValues, errors } = hookFormMethods

  const onSubmit = (data) => {
    dispatch(setFormData({ ...formData, ...data }))
    dispatch(setCurrentTab(5))
  }

  const handlePreviousClick = () => {
    const values = getValues()
    dispatch(setFormData({ ...formData, ...values }))
    dispatch(setCurrentTab(3))
  }

  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <Box className={classes.scrolledArea}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: '14px', height: 'inherit' }}>
          <ControlledTextField
            labelTitle="     ,          ?"
            fieldName="conditions_and_tasks"
            control={control}
            error={!!errors.conditions_and_tasks?.message}
            helperText={errors.conditions_and_tasks?.message}
            placeholder=" "
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
            isRequired
          />
          <ControlledTextField
            labelTitle="       hh     &nbsp;&mdash; , , "
            fieldName="vacancy_url"
            control={control}
            error={!!errors.vacancy_url?.message}
            helperText={errors.vacancy_url?.message}
            placeholder=" "
            maxWidth={476}
          />
          <ControlledTextField
            labelTitle="      (hard skills)   (          )?"
            fieldName="mandatory_requirements"
            control={control}
            error={!!errors.mandatory_requirements?.message}
            helperText={errors.mandatory_requirements?.message}
            placeholder=" "
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
            isRequired
          />
          <ControlledTextField
            labelTitle="    ,   ?"
            fieldName="optional_requirements"
            control={control}
            error={!!errors.optional_requirements?.message}
            helperText={errors.optional_requirements?.message}
            placeholder=" "
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
          />
          <ControlledTextField
            labelTitle="   ?"
            fieldName="selection_stages"
            control={control}
            error={!!errors.selection_stages?.message}
            helperText={errors.selection_stages?.message}
            placeholder=" "
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
            isRequired
          />
          <ControlledTextField
            labelTitle="    / ?"
            fieldName="team_help"
            control={control}
            error={!!errors.team_help?.message}
            helperText={errors.team_help?.message}
            placeholder=" "
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
          />
          <ControlledTextField
            labelTitle="       (              )?"
            fieldName="feedback_giving"
            control={control}
            error={!!errors.feedback_giving?.message}
            helperText={errors.feedback_giving?.message}
            placeholder=" "
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
            isRequired
          />
          <ControlledTextField
            labelTitle="       ( // )?        "
            fieldName="contact"
            control={control}
            error={!!errors.contact?.message}
            helperText={errors.contact?.message}
            placeholder=" "
            multiline
            rows={3}
            maxWidth={476}
            textClass={classes.textAreaLarge}
          />
        </Box>
      </Box>
      <div className={classes.buttonContainer}>
        <Button type="button" className={classes.previousButton} onClick={handlePreviousClick}>
          
        </Button>
        <Button className={classes.submitButton} type="submit">
          
        </Button>
      </div>
    </form>
  )
}

export default React.memo(TabPF4)
