import * as yup from 'yup'
import { urlOption, urlOptionRequired, workVacancyOption, multipleValuesOptions } from '../schemas'

const FILE_SIZE = 2097152

class PartnersFormSchema {
  static readonly zeroTab = yup.object().shape({
    hr_profile: yup.string().required(' '),
  })

  static readonly firstTab = yup.object().shape({
    company_name: yup.string().required('  '),
    company_url: urlOptionRequired,
    company_type: yup.number().required('  '),
    company_scope: yup.number().required('  '),
    company_size: yup.number().required('  '),
  })

  static readonly secondTab = yup.object().shape({
    courses: yup.array().min(1, '    ').required('    '),
    position: workVacancyOption,
    candidate: yup.string().required('  '),
    salary: yup
      .number()
      .max(1000000000, '       1000000000')
      .moreThan(-1, '     ')
      .transform((value, original) => (original == null || original === '' ? null : value))
      .nullable(true)
      .typeError('    0-9')
      .when('salary_max', {
        is: (val: any) => !val,
        then: (theme) => theme.required('     '),
        otherwise: (theme) => theme.max(yup.ref('salary_max'), '    ""'),
      })
      .when('isSalaryNotRequired', {
        is: true,
        then: (theme) => theme.notRequired(),
      }),
    salary_max: yup
      .number()
      .max(1000000000, '       1000000000')
      .moreThan(-1, '     ')
      .transform((value, original) => (original == null || original === '' ? null : value))
      .nullable(true)
      .typeError('    0-9'),
    company_confidential_url: urlOption,
  })

  static readonly thirdTab = yup.object().shape({
    work_format: yup.number().required('  '),
    cities: multipleValuesOptions,
    registration_in_the_state: yup.boolean().required(' '),
    employment: yup.number().required(' '),
    none_experience: yup.boolean().required(' '),
    none_education: yup.boolean().required(' '),
    test_task_url: urlOption,
  })

  static readonly fourthTab = yup.object().shape({
    conditions_and_tasks: yup.string().required(' '),
    mandatory_requirements: yup.string().required(' '),
    selection_stages: yup.string().required(' '),
    feedback_giving: yup.string().required(' '),
    vacancy_url: urlOption,
  })

  static readonly fifthTab = yup.object().shape({
    company_logo: yup
      .mixed()
      .test(
        'fileSize',
        `   ${FILE_SIZE / 1048576} MB`,
        (value) => !value || typeof value === 'string' || (value && value.size <= FILE_SIZE),
      ),
    big_cover: yup
      .mixed()
      .test(
        'fileSize',
        `   ${FILE_SIZE / 1048576} MB`,
        (value) => !value || typeof value === 'string' || (value && value.size <= FILE_SIZE),
      ),
    small_cover: yup
      .mixed()
      .test(
        'fileSize',
        `   ${FILE_SIZE / 1048576} MB`,
        (value) => !value || typeof value === 'string' || (value && value.size <= FILE_SIZE),
      ),
  })

  static readonly sixthTab = yup.object().shape({
    source_info: yup.string().required(' '),
    pub_agree: yup.boolean().required(' '),
  })
}

export default PartnersFormSchema
